<script>
    export default {
        name: "order_edit",

        data:function(){

            return{

                order_id:0,
            }
        },

        methods:{

            order_info:function(){
                var url = this.config.web_url + '/admin.php?r=order/info';
                url += '&order_id='+ this.order_id;
                var t = this;

                this.axios.get( url ).then(function( res ){

                    t.order_list = res.data;

                });
            }

        },

        created:function(){

            var params = this.$route.params;
            if( params.order_id == undefined ){
                this.order_id = 0;
            }else{
                this.order_id = params.order_id;
                this.order_info();
            }
        }
    }
</script>

